/* CSS reset */
* {
  box-sizing: border-box;
}
*:before,
*:after {
  box-sizing: border-box;
}
* {
  margin: 0;
  padding: 0;
}
ul,
ol {
  list-style: none;
}
a {
  color: inherit;
  text-decoration: none;
}
img {
  max-width: 100%;
  max-height: 100%;
}
/* style */
body {
  background: #eeeeee;
}
.globalHeader {
  margin: 20px;
}
.searchForm {
  display: flex;
  justify-content: space-between;
}
.searchForm > input {
  height: 40px;
  width: 100%;
  margin-right: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 0 10px;
}
.searchForm > button {
  white-space: nowrap;
  /* 强制文字不换行 */
  padding: 0 28px;
  border: none;
  background: #0a80b3;
  border-radius: 4px;
  color: #ffffff;
  font-size: 16px;
}
.siteList {
  margin: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.siteList > li {
  margin-bottom: 10px;
}
.siteList .site {
  width: 160px;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 20px 0;
}
.siteList .site > .logo {
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 64px;
}
.siteList .site > .link {
  font-size: 14px;
  margin-top: 4px;
}
.siteList .addButton {
  border: 1px solid blue;
  width: 160px;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 20px 0;
}
.siteList .addButton .iconWrapper {
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.siteList .addButton .text {
  font-size: 14px;
  margin-top: 4px;
}
.siteList .addButton .icon {
  width: 48px;
  height: 48px;
}
